import react from 'react'
import axios from 'axios'

class Login extends React.Component {
    render(){
        return(
            <div>
                <table>
                    <tr>
                        <td>username</td>
                        <td>
                            <input type="text" placeholder="username"></input>
                        </td>
                    </tr>
                    
                    <tr>
                        <td>password</td>
                        <td>
                            <input type="password" placeholder="password"></input>
                        </td>
                    </tr>
                </table>
            </div>
        )
    }
}

export default Login